svg circle {
  transition: fill 0.2s ease-in;
}
svg line {
  transition: stroke 0.2s ease-in;
}
.graph-svg {
  display: block;
  margin: 0 auto;
  max-width: 500px;
  max-height: 500px;
}
.graph-section,
.about-section {
  margin: 0 auto;
}
.graph-section-header {
  text-align: center;
}
.competition {
  text-align: center;
}
.competition .competition-graphs {
  margin-top: 15px;
}
.competition .competition-graphs .graph-section {
  margin: 0;
}
.competition h2.title {
  margin-bottom: 0;
  font-size: 18px;
}
.competition .title-icon {
  top: 1px;
  margin-right: 4px;
  font-size: 18px;
}
.competition .title-icon.is-bfs {
  margin-right: 3px;
}
.competition .graph-article {
  padding: 0;
}
.legend-list {
  max-width: 500px;
  margin: 0 auto;
  padding-left: 0;
  text-align: center;
}
.legend-list.is-edge-case {
  max-width: 700px;
}
.legend-item {
  display: inline-block;
}
.legend-item + .legend-item {
  margin-left: 10px;
}
.legend-item .circle,
.article-list-item .circle {
  display: inline-block;
  border-radius: 50%;
  width: 11px;
  height: 11px;
  margin-right: 3px;
}
.legend-item .circle {
  vertical-align: middle;
}
.legend-item .text {
  display: inline-block;
  vertical-align: middle;
}
.legend-item .circle.is-unvisited,
.article-list-item .circle.is-unvisited {
  background-color: #7b8f99;
}
.legend-item .circle.is-obstacle,
.article-list-item .circle.is-obstacle {
  background-color: #ff69af;
}
.legend-item .circle.is-visited,
.article-list-item .circle.is-visited {
  background-color: #448ee4;
}
.legend-item .circle.is-blocked,
.article-list-item .circle.is-blocked {
  background-color: wheat;
}
.legend-item .circle.is-path,
.article-list-item .circle.is-path {
  background-color: #bff128;
}
.legend-item .circle.is-target,
.article-list-item .circle.is-target {
  background-color: #ff8833;
}
.legend-item .circle.is-path-target,
.article-list-item .circle.is-path-target {
  width: 13px;
  height: 13px;
  background-color: #bff128;
  border-radius: 50%;
  border: 2px solid #ff8833;
}
.legend-item .circle.is-visited-root,
.article-list-item .circle.is-visited-root {
  width: 13px;
  height: 13px;
  background-color: #448ee4;
  border-radius: 50%;
  border: 2px solid #ff8833;
}
.legend-item .circle.is-blocked-root,
.article-list-item .circle.is-blocked-root {
  width: 13px;
  height: 13px;
  background-color: #f5deb3;
  border-radius: 50%;
  border: 2px solid #ff8833;
}
.container.dark-mode {
  background: #3a3f41;
  color: #fff;
}
.container.dark-mode a {
  color: #fff;
}
.container.dark-mode .nav-link,
.container.dark-mode .article-list-item-link {
  color: #fff;
}
.container.dark-mode .title {
  color: #fff;
}
.nav {
  margin: 0 auto;
  text-align: center;
}
.nav.is-main {
  display: flex;
  justify-content: center;
  max-width: 600px;
  font-size: 16px;
}
.nav.is-main .nav-link {
  font-size: 20px;
}
.nav.is-secondary .nav-link + .nav-link {
  margin-left: 5px;
}
.nav-link {
  transition: color 0.3s;
}
.nav-link.is-active {
  color: #ff69af !important;
  cursor: default;
  pointer-events: none;
}
.nav-link + .nav-link {
  margin-left: 30px;
}
.nav-text.is-mobile .anticon {
  margin-right: 4px;
  position: relative;
  top: 1px;
}
.nav-text.is-desktop {
  display: none;
}
.btn-theme {
  display: block;
  width: 100%;
  max-width: 320px;
  height: 32px;
  margin: 0 auto 10px;
}
.btn-reload {
  display: block;
  margin: 5px auto 10px;
  width: 125px;
  height: 32px;
  font-size: 17px;
}
.title {
  display: inline-block;
  margin-bottom: 4px;
  position: relative;
  text-align: center;
  transition: color 0.3s;
}
.title-icon {
  position: relative;
  top: 1px;
  font-size: 26px;
  color: #448ee4;
}
.graph-article {
  max-width: 500px;
  margin: 10px auto -5px;
  text-align: center;
}
.graph-article a {
  text-decoration: underline;
}
.graph-article p {
  display: inline-block;
  margin-bottom: 5px;
  transition: color 0.3s;
}
.article-list {
  padding-left: 0;
  list-style: none;
}
.article-list.has-links .anticon {
  position: relative;
  top: 1px;
  font-size: 17px;
}
.article-list.has-links .article-list-item + .article-list-item {
  margin-top: 20px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}
body.has-error #root,
body.has-error .container {
  height: 100%;
}
body.has-error .main {
  max-width: 600px;
  margin: 0 auto;
  padding-top: 20px;
}
body.has-error .container.dark-mode .error-sub-title {
  color: #fff;
}
body.has-error .error-title {
  color: #ff69af;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
a {
  color: rgba(0, 0, 0, 0.85);
}
a:hover {
  color: #448ee4 !important;
}
p a,
ul a {
  text-decoration: underline !important;
}
.container {
  min-width: 320px;
  padding: 0 10px;
  transition: background 0.3s;
}
.header {
  position: relative;
  margin-bottom: 15px;
  padding-top: 10px;
}
.main {
  padding-bottom: 15px;
}
.ant-message-notice.message-dark .ant-message-notice-content {
  background-color: #61666b;
  color: #fff;
}
.about-section {
  max-width: 315px;
}
@media screen and (min-width: 865px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
  .header {
    margin-bottom: 25px;
    padding-top: 15px;
  }
  .main {
    padding-bottom: 20px;
  }
  .btn-theme {
    position: absolute;
    top: 15px;
    right: 0;
    width: auto;
    height: auto;
    margin-bottom: 0;
  }
  .nav.is-main {
    justify-content: space-around;
  }
  .nav.is-main .nav-link {
    font-size: 1em;
  }
  .nav-text.is-mobile {
    display: none;
  }
  .nav-text.is-desktop {
    display: inline-block;
  }
  .nav-link + .nav-link {
    margin-left: 0;
  }
  .btn-reload {
    position: absolute;
    left: 103%;
    top: 5px;
    width: auto;
    height: auto;
    margin: 0;
    font-size: 15px;
  }
  .competition .competition-graphs {
    display: flex;
    justify-content: center;
  }
  .competition .competition-graphs .graph-section {
    width: 50%;
    margin: 0 30px;
  }
  .competition .btn-reload {
    top: 4px;
    left: 104%;
  }
  .competition .graph-article {
    padding: 0 15px;
  }
  .graph-section {
    max-width: 500px;
  }
  .about-section {
    max-width: 520px;
  }
  .graph-wrapper {
    height: 500px;
  }
}
